const state = {
  callingAPI: false,
  searching: '',
  authenticated: false,
  provider: 'fireBase',
  user: null,
  token: null,
  userInfo: {
    email: '',
    verified: '',
    name: '',
    nickName: '',
    picture: '',
    id: '',
    since: ''
  }
}

const getters = {
  isAuth: state => state.authenticated,
  getToken: state => state.storeList,
  getUser: state => state.userInfo,
  getProvider: state => state.provider,
  getUserImage: state => state.userInfo.picture,
  getToken: state => state.token
}

const mutations = {
  SET_INFO (state, user) {
    state.userInfo.nickName = user.displayName ? user.displayName: user.email
    state.userInfo.email = user.email
    state.userInfo.verified = user.emailVerified
    state.userInfo.picture = user.photoURL || 'http://findicons.com/files/icons/719/crystal_clear_actions/64/organizer.png'
    state.userInfo.id = user.uid
    state.authenticated = true
  },
  SET_EMPTY () {
    state.userInfo.email = null
    state.userInfo.verified = null
    state.userInfo.picture = null
    state.userInfo.id = null
    state.authenticated = false
    state.token = null
  },
  SET_PROVIDER (state, provider) {
    state.provider = provider
  },
  SET_TOKEN (state, token) {
    state.token = token
    state.authenticated = true
  }
}

const actions = {
  setUser ({commit}, user) {
    commit('SET_INFO', user)
  },
  clearUser ({commit}) {
    commit('SET_EMPTY')
  },
  setProvider ({commit}, provider) {
    commit('SET_PROVIDER', provider)
  },
  setToken ({commit}, token) {
    commit('SET_TOKEN', token)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



