<template>
  <div class="index" v-cloak>
    <app-header2></app-header2>
    <div id="content" style="padding-bottom: 300px;">
      <div class="container-fluid">
        <div class="row">
          <div id="carouselExampleIndicators" v-if="this.$route.path.split('/')[1] !== 'detail'" class="carousel slide hidden-md-down" style="float: none; margin: 0 auto;" data-ride="carousel">
            <ol class="carousel-indicators">
              <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
              <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
            </ol>
            <div class="carousel-inner" role="listbox">
              <div class="carousel-item active" style="padding-top:10px; padding-bottom:10px;">
                <img class="d-block img-fluid" style="max-height:220px; object-fit: cover;-webkit-user-select: none;background-position: 0px 0px, 10px 10px;background-size: 20px 20px;background-image:linear-gradient(45deg, #eee 25%, transparent 25%, transparent 75%, #eee 75%, #eee 100%),linear-gradient(45deg, #eee 25%, white 25%, white 75%, #eee 75%, #eee 100%);cursor: zoom-in;" src="https://cdn.best.bargains/banners/2.png" width="1400" height="225">
              </div>
              <div class="carousel-item" style="padding-top:10px; padding-bottom:10px;">
                <img class="d-block img-fluid" style="max-height:220px; object-fit: cover;-webkit-user-select: none;background-position: 0px 0px, 10px 10px;background-size: 20px 20px;background-image:linear-gradient(45deg, #eee 25%, transparent 25%, transparent 75%, #eee 75%, #eee 100%),linear-gradient(45deg, #eee 25%, white 25%, white 75%, #eee 75%, #eee 100%);cursor: zoom-in;" src="https://cdn.best.bargains/banners/3.png" width="1400" height="225">
              </div>
            </div>
            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
              <span class="carousel-control-prev-icon" aria-hidden="true"></span>
              <span class="sr-only">Previous</span>
            </a>
            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
              <span class="carousel-control-next-icon" aria-hidden="true"></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
        </div>
      </div>
      <div class="main_body">
        <div class="container">
          <div class="row" v-if="this.$route.path.split('/')[1] === 'detail'" style="padding-left: 5px;">
            <ol class="breadcrumb" style="padding: 0.3rem 0rem; margin-bottom:0.3rem">
              <li class="breadcrumb-item"><router-link to="/" style="color: #0072bc;">Home</router-link></li>
              <li class="breadcrumb-item">Bargains</li>
            </ol>
            <!-- <p class="pull-left" style="font-size:20px; color: #616161; margin-bottom:0; font-family: 'Open Sans',Arial,sans-serif; font-weight: 300;"><strong>Today's Deals</strong></p>-->
          </div>
          <div class="row">
            <router-view></router-view>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader2 from './Header'
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      toggle: true,
      swiperOption: {
        paginationClickable: true,
        direction: 'vertical',
        spaceBetween: 0
      }
    }
  },
  components: {
    AppHeader2
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

div.container-fluid {
  padding-left:0;
  padding-right:0;
}

div.card-body a {
  color: #0072bc;
}

.breadcrumb-item {
  margin-right: 0;
  margin-left: 0;
}

ol.breadcrumb {
  background: transparent;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



