/*Corporate Layout*/
import fireBase from '../firebase/vueFire'
import Store from '../vuex/index'
import { checkAuth } from '../util/auth-helper'

function RequireAuth(to, from, next) {
  if (!Store.getters.isAuth) {
    next({
      path: '/',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
}

function isAuth(to, from, next) {
  var fire = fireBase
  if (checkAuth()) {
    if (!Store.getters.isAuth) {
      fire.auth().onAuthStateChanged(function(user) {
        if (user) {
          Store.dispatch('setUser', user)
          next()
        } else {
          next()
        }
      });
    } else {
      next()
    }
  } else {
    next()
  }
}


const routes = [
  {
    path: '/',
    component: index,
    meta: 'Best daily bargains and offers',
    beforeEnter: isAuth,
    children: [
      {
        path: '',
        component:ParentContent,
        children: [
          {
            path: '',
            component: AppContent,
            meta: 'Best daily bargains and offers',
            children: [
              {
                path: '',
                component: AllBargains,
                name: 'allBargains',
                meta: 'Best daily bargains and offers'
              },
              {
                path: '/last24h',
                component: todayBargains,
                name: 'todayBargains',
                meta: 'Last 24 hours'
              },
              {
                path: '/popular',
                component: bestBargains,
                name: 'bestBargains',
                meta: 'Top Deals'
              },
              {
                path: '/last7d',
                component: last7d,
                name: 'last7d',
                meta: 'Last 7 Days'
              },
              {
                path: '/editors',
                component: editorPick,
                name: 'editorPick',
                meta: 'Editor\'s Picks'
              },
              {
                path: '/bookmarked',
                beforeEnter: RequireAuth,
                component: bookmark,
                name: 'bookmark',
                meta: 'Bookmarked Bargains'
              },
              {
                path: '/liked',
                beforeEnter: RequireAuth,
                component: likes,
                name: 'likes',
                meta: 'Liked Bargains'
              }
            ]
          },
          {
            path: '/detail/:id/:title',
            component: Detail,
            name: 'bargainDetail'
          },
          {
            path: '/offers',
            component: StoreDeals,
            meta: 'Store Deals',
            children: [
              {
                path: '',
                component: ListDeals,
                name: 'ListDeals',
                meta: 'Store Deals'
              }
            ]
          }
        ]
      },
      {
        path: '/accverify',
        component: verify,
        name: 'accverify'
      },
      {
        path: '/user',
        component: User,
        beforeEnter: RequireAuth,
        children: [
          {
            path: '',
            redirect: {name: 'UserBargains'}
          },
          {
            path: 'bargains',
            component: UserBargains,
            name: 'UserBargains',
            meta: 'User Bargains',
            children: [
              {
                path: 'add',
                component: AddBargain,
                name: 'addBargain',
                meta: 'Add Bargain'
              },
              {
                path: 'edit',
                component: EditBargain,
                name: 'editBargain',
                meta: 'Edit Bargain'
              }
            ]
          },
          {
            path: 'profile',
            component: UserProfile,
            name: 'UserProfile',
            meta: 'User Profile',
            children: [
              {
                path: 'update',
                component: UpdatePhoto,
                name: 'UpdatePhoto',
                meta: 'Update Photo'
              },
              {
                path: 'password',
                component: PasswordReset,
                name: 'passwordReset',
                meta: 'Password Reset'
              }
            ]
          }
        ]
      }
    ]
  }
]

export default routes



