    <nav class="navbar navbar-toggleable-md second-bar navbar-inverse" style="background-color:#1F2D3D; padding-top:0;  padding-bottom:0; height: 51px; z-index: 2000;">
      <button class="navbar-toggler navbar-toggler-left" type="button" data-toggle="collapse" data-target="#my-navbar-collapse"
