          </ul>
        </div>
        <div class="d-inline-block pull-right" style="z-index: 10;">
          <ul v-if="onLoad" class="navbar-nav mr-auto login-auth pull-right first-d hidden-sm-down">
            <li class="nav-item pull-right">
              <button type="button" class="btn btn-raised nav-link" style="background:none; border: 1px solid #03a9f4; font-size: 12px;" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" @click="toggleMenu('register')">
                Sign Up
              </button>
            </li>
            <li class="nav-item pull-right" style="margin-left: 0;">
              <button type="button" class="btn btn-raised btn-info nav-link" style="font-size: 12px;" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" @click="toggleMenu('login')">
