            <router-link to="/user/bargains" exact data-toggle="collapse" data-target=".navbar-collapse.show">
              <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;">
                <i class="fa fa-cog" aria-hidden="true" style="font-size:1.1em;"></i><span style="margin-left:10px;">My Activities</span>
