              </li>
            </router-link>
            <router-link :to="{name: 'likes'}" exact data-toggle="collapse" data-target=".navbar-collapse.show">
              <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;">
                <i class="fa fa-thumbs-o-up" aria-hidden="true" style="font-size:1.1em;"></i><span style="margin-left:10px;">Liked</span>
              </li>
            </router-link>
          </ul>
        </div>
      </div>
      <ul class="list-group  hidden-md-up">
        <router-link :to="{name: 'allBargains'}" exact data-toggle="collapse" data-target=".navbar-collapse.show">
          <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;">
            <i class="fa fa-circle-o" aria-hidden="true"></i><span style="margin-left:10px;">All Bargains</span>
          </li>
        </router-link>

        <router-link :to="{name: 'todayBargains'}" exact data-toggle="collapse" data-target=".navbar-collapse.show">
          <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;">
            <i class="fa fa-circle-o" aria-hidden="true"></i><span style="margin-left:10px;">Today's Bargains</span>
          </li>
        </router-link>

        <router-link :to="{name: 'bestBargains'}" exact data-toggle="collapse" data-target=".navbar-collapse.show">
          <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;">
            <i class="fa fa-circle-o" aria-hidden="true"></i><span style="margin-left:10px;">Best Bargains</span>
          </li>
        </router-link>

        <router-link :to="{name: 'ListDeals'}" exact data-toggle="collapse" data-target=".navbar-collapse.show">
          <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;">
            <i class="fa fa-circle-o" aria-hidden="true"></i><span style="margin-left:10px;">Offers</span>
          </li>
        </router-link>
        <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;" @click="signOut" v-if="isAuth" data-toggle="collapse" data-target=".navbar-collapse.show">
          <i class="fa fa-power-off" aria-hidden="true" style="font-size:1.2em;"></i><span style="margin-left:10px;">Logout</span>
        </li>
        <li class="list-group-item reset-col" style="padding: 7px 0 7px 15px;" v-else @click="toggleMenu('login')" data-toggle="collapse" data-target=".navbar-collapse.show">
          <i class="fa fa-power-off" aria-hidden="true" style="font-size:1.1em;"></i><span style="margin-left:10px;">Sign In</span>
        </li>
      </ul>
    </div>
    <nav class="navbar navbar-toggleable-md second-bar navbar-inverse sub-navbar hidden-md-down" style="background-color:#F9FAFC;padding:0; box-shadow: 0 2px 2px -1px #757575;">
      <div class="container text-center" style="padding-left: 4px;">
        <ul class="navbar-nav mr-auto" v-if="isOffer">
          <li class="nav-item">
            <router-link :to="{name: 'allBargains'}" exact class="nav-link">All</router-link>
          </li>
          <li class="nav-item">
            <router-link :to="{name: 'todayBargains'}" exact class="nav-link">Last 24 hours</router-link>
          </li>
          <li class="nav-item">
            <router-link :to="{name: 'last7d'}" exact class="nav-link">Last 7 days</router-link>
          </li>
          <li class="nav-item">
            <router-link :to="{name: 'bestBargains'}" exact class="nav-link">Popular</router-link>
          </li>
          <li class="nav-item">
            <router-link :to="{name: 'editorPick'}" exact class="nav-link">Editor picks</router-link>
          </li>
          <li class="nav-item" v-if="isAuth">
            <router-link :to="{name: 'bookmark'}" exact class="nav-link">Bookmarked</router-link>
          </li>
          <li class="nav-item" v-if="isAuth">
            <router-link :to="{name: 'likes'}" exact class="nav-link">Liked</router-link>
          </li>
        </ul>
        <ul class="navbar-nav mr-auto" v-else>
          <li class="nav-item">
            <router-link :to="{name: 'ListDeals'}" exact class="nav-link">All Offers</router-link>
          </li>
        </ul>
      </div>
    </nav>
    <login :show="isShow" v-on:onCancel="onCancel" :setActive="active" v-on:setTab="setTab"></login>
  </div>

</template>
<style>

  div.header div.modal-dialog {
    margin: 30px auto;
  }

  .header .list-group-item {
    background: none;
    color: white;
    font-size: 14px;
    border: none;
    padding-left: 0 !important;
    margin: 0;
    border-bottom: 1px dashed #616161;
  }
  .hidden-sm-up {
    display: block !important;
  }



  @media (min-width: 767px) {
    .hidden-sm-up {
      display: none !important;
    }
  }
  .header .list-group-item:hover {
    background-color: #324057;
    cursor: pointer;
  }

  .header .navbar-toggleable-md .navbar-nav {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
  }

  .navbar-brand {
    font-family: 'Vidaloka', serif;
    margin-left: auto;
    margin-right: auto;
  }


  @media (min-width: 0px) {
    .header .container {
      width: inherit;
      margin: auto;
    }

    .header .hidden-md-up {
      display: block!important;
    }

    .navbar-brand {
      font-size: 1.5em;
    }
    .header ul.second-d {
      margin-top: 10px;
    }

    .navbar-brand img {
      height: 25px;
      object-fit: scale-down;
    }
  }

  @media (min-width: 341px) {

  }

  @media (min-width: 412px) {
    .navbar-brand {
      font-size: 1.8em;
    }
    .navbar-brand img {
      width: inherit;
      height: 1.5em;
      object-fit: inherit;
    }
    .header .container {
      position: inherit;
      left: inherit;
    }
  }





  @media (min-width: 768px) {
    .header .container {
      width: 665px;
      max-width: 100%;
      margin-right: 0;
    }
  }

  @media (min-width: 835px) {
    .header .container {
      width: 750px;
      max-width: 100%;
      margin-right: 0;
    }
  }


  @media (min-width: 992px) {
    .navbar-brand {
      font-size: 1.8em;
    }
    ul.first-d {
      margin-top:0 !important;
    }
    .header ul.second-d {
      margin-top: 0px;
    }
    .header .container {
      margin-left: auto !important;
      margin-right: auto !important;;
      width: 960px;
      max-width: 100%;
    }
    .header .hidden-md-up {
      display: none!important;
    }
  }

  @media (min-width: 1196px) {
    .header .container {
      width: 100%;
    }
  }


  @media (min-width: 1218px) {
    .header .container {
      width: 1200px;
      max-width: 100%;
    }
  }

  @media (min-width: 1316px) {
    .header .container {
      width: 1300px;
    }
  }

  @media (min-width: 1425px) {
    .header .container {
      width: 1410px;
    }
  }

  @media (min-width: 1196px) {
    nav.sub-navbar div.container {
      max-width: 1000px;
      width: 1000px;
    }
  }

  @media (min-width: 1218px) {
    nav.sub-navbar div.container {
      max-width: 1100px;
      width: 1100px;
    }
  }



  .sub-navbar ul.navbar-nav li.nav-item a{
    color: #212121;
  }

  .sub-navbar ul.navbar-nav li.nav-item a:hover{
    color: #107896;
    font-weight:bold;
  }

  .sub-navbar ul.navbar-nav li.nav-item a.router-link-active{
    color: #107896;
    font-weight:bold;
  }

 .navbar.second-bar li{
    border-radius: 0;
    padding-right: 15px;
 }

 .login-auth button {
   width: 100% !important;
   min-width: 100px;
   padding: 8px 20px;
 }

 li.user-drop-down button{
  color: white !important;
  margin:0;
  padding:0;
  text-transform: inherit;
 }
 div.header div.dropdown-menu {
  padding:0;
  border-radius: 4px !important;
 }


 div.dropdown-menu > div.row >  a.dropdown-item {
  border-radius: 0;
  font-size: 13px;
  text-transform: none;
  border-bottom:1px solid #ccc;
 }

  div.container {
    padding-left:0;
    padding-right:0;
  }

  .img-circle {
      border-radius: 50%;
  }

  .img-thumbnail {
    padding: .25rem;
    background-color: #fafafa;
    border: 1px solid #ddd;
    border-radius: .125rem;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    box-shadow: 0 1px 2px rgba(0,0,0,.075);
    display: inline-block;
    max-width: 100%;
    height: auto;
  }

  span.grid {
    color: #2196F3 !important;
  }



 .d-inline-block li.nav-item a.nav-link:hover {
  text-decoration: underline;
 }



 .second-bar > div.container > div.navbar-collapse > ul > li a.nav-link{
  border-radius: 0;
  font-size: 13px;
  text-transform: uppercase;
  padding-top:13px;
  padding-bottom:13px;
  font-weight: lighter;
  padding-left:10px;
  color: white;
  box-shadow: inset 0 0 0 0 #03A9F4;
  -webkit-transition: all ease 0.5s;
	-moz-transition: all ease 0.5s;
	transition: all ease 0.5s;
 }

 .nav-link.active {
  background-color: white;
 }

 .dropdown-toggle::after {
  margin-left: .8em;
 }


 div.dropdown-menu {
  border-top: 0;
  border-left: 0;
  margin-top: 0;
  padding-top: 0;
  border-radius: 0;
 }


 div.heaader {
  font-family: "Open Sans",arial,sans-serif;
 }

 .second-bar > div.container > div.navbar-collapse > ul > li a.nav-link:hover,.second-bar > div.container >  div.navbar-collapse > ul > li a.nav-link:active,  .second-bar > div.container >  div.navbar-collapse > ul > li a.nav-link:focus{
  color: white;
  font-weight:bold;
  box-shadow: inset 0 100px 0 0 #03A9F4;

 }

  .sticky.is-sticky {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
  }

  /* Overriding styles */

  ::-webkit-input-placeholder {
    font-size: 12px!important;
    font-weight: 500;
  }

  :-moz-placeholder { /* Firefox 18- */
    font-size: 12px!important;
    font-weight: 500;
  }
  ::-moz-placeholder {  /* Firefox 19+ */
    font-size: 12px!important;
    font-weight: 500;
  }

  .nav-bottom {
    box-shadow: 0px 1px 5px black;
  }

</style>
<script>
import fireBase from '../../firebase/vueFire'
import login from './Login'
export default {
  name: 'AppHeader2',
  data () {
    return {
      isShow: false,
      active: '',
      onLoad: false,
      callBack: false,
      init: true
    }
  },
  components: {
    login
  },
  methods: {
    toggleMenu : function(data) {
      this.active = data
      this.isShow = !this.isShow
    },
    onCancel: function () {
      this.isShow = !this.isShow
    },
    setTab: function (value) {
      this.active = value
    },
    signOut: function () {
      fireBase.auth().signOut().then(() =>{
        this.$root.$refs.toastr.Add({
          title: "", // Toast   Title
          msg: "Successfully Logged Out!", // Message
          clickClose: false, // Click Close Disable
          timeout: 1000, // Remember defaultTimeout is 5 sec..
          position: "toast-top-center", // Toast Position.
          type: "success" // Toast type
        })

        this.$store.dispatch('clearUser')
        this.$root.$emit('user:logOut')
      }, function(error) {
         console.log('Error Logged Out')
      });
    }
  },
  computed: {
    isOffer () {
      if (this.$route.path === '/offers/') {
        return false
      } else {
        return true
      }
    },
    isAuth () {
      if (this.callBack) {
        var auth = this.$store.getters.isAuth
        auth ? this.onLoad = false : this.onLoad = true
      }
      return auth
    },
    getUser () {
      var user = this.$store.getters.getUser
      return user.nickName.split('@')[0]
    },
    getEmail () {
      return this.$store.getters.getUser
    },
    getUserImage () {
      var image = this.$store.getters.getUserImage || 'http://findicons.com/files/icons/719/crystal_clear_actions/64/organizer.png'
      return image
    }
  },
  mounted () {
    fireBase.auth().onAuthStateChanged( (user) => {
      this.callBack = true
      if (user) {
        user.getToken().then((data) =>{
          this.$store.dispatch('setToken', data)
          localStorage.setItem('fire_token', data)
        });
        this.$store.dispatch('setUser', user)
        if (this.init) {
          this.$root.$refs.toastr.Add({
            title: "", // Toast Title
            msg: "Successfully Logged In!", // Message
            clickClose: false, // Click Close Disable
            timeout: 1000, // Remember defaultTimeout is 5 sec..
            position: "toast-top-center", // Toast Position.
            type: "success" // Toast type
          })
          this.init = false;
        }
      } else {
        this.init = false;
        localStorage.removeItem('fire_token')
      }
    });
  }
}
</script>



