<script>
  import Index from './components/layout/Index'

  export default {
    name: 'App',
    color: 'green',
    size: '30px',
    loading: true,
    components: {
      Index
    },
    metaInfo: function () {
      return {
        meta: [
          {property: 'twitter:title', content: 'Best Bargains - Best Daily Bargains', vmid: 'twitterTitle'},
          {property: 'twitter:description', content: 'Find Best Daily Bargains', vmid: 'twitterDesc'},
          {property: 'twitter:url', content: location.href, vmid: 'twitterUrl'},
          {property: 'twitter:image', content: 'https://bestbargains-dev.firebaseapp.com/static/favicon/ms-icon-310x310.png', vmid: 'twitterImg' },
          {property: 'og:url', content: location.href, vmid: 'urlOg'},
          {property: 'og:description', content: 'Find Best Daily Bargains', vmid: 'descOg'},
          {
            property: 'og:image',
            content: 'https://bestbargains-dev.firebaseapp.com/static/favicon/ms-icon-310x310.png',
            vmid: 'imgOg'
          },
          {property: 'og:title', content: 'Best Bargains - Best Daily Bargains', vmid: 'titleOg'},
          {property: 'og:type', content: 'website'},
          {itemprop: 'site_name', content: 'BestBargains'},

        ]
      }
    },
    mounted () {
      this.$root.$on('user:logOut', () => {
        this.$router.go({
          name: 'allBargains',
          query: {
            t: new Date()
          }
        })
      })
      this.$root.$on('user:logIn', () => {
        this.$router.go({
