const state = {
  storeList: [],
  storeObj: [],
  isEmpty: false
}

const getters = {
  isEmpty: state => state.isEmpty,
  getList: state => state.storeList,
  getObj: state => state.storeObj
}

const mutations = {
  setList (state, list) {
    state.storeList = list
    state.isEmpty = true
  },
  setObj (state, list) {
    state.storeObj = list
    state.isEmpty = true
  }
}

const actions = {
  setList ({commit}, list) {
    commit('setList', list)
  },
  setObj ({commit}, list) {
    commit('setObj', list)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



