// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import routes from './router/routes'
import Resource from 'vue-resource'
import Meta from 'vue-meta'
import store from './vuex/index'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import PulseLoader from 'vue-spinner/src/ClipLoader.vue'
import Toastr from 'vue-toastr'
import InfiniteLoading from 'vue-infinite-loading'
import _ from 'lodash'
import DataTable from 'components/datatable/main.js'
import VeeValidate from 'vee-validate'
import VueQuillEditor from 'vue-quill-editor'
import VueRecaptcha from 'vue-recaptcha'
import VueSweetAlert from 'vue-sweetalert'
import SocialSharing from 'vue-social-sharing'
import { Button, Select, Slider, Input, DatePicker, Pagination, Dialog, Upload, Option, InputNumber, Rate, Alert, Tooltip, Switch } from 'element-ui'
import lang from 'element-ui/lib/locale/lang/en'
import locale from 'element-ui/lib/locale'
import VueAnalytics from 'vue-analytics'
import 'element-ui/lib/theme-default/index.css'
import VueProgressBar from 'vue-progressbar'
import VueResourceProgressBarInterceptor from 'vue-resource-progressbar-interceptor'
import VueAdsense from 'components/adsense/adsense.vue'


Vue.use(VueAnalytics, {
  id: process.env.GA
})
const path = process.env.API_PATH
locale.use(lang)

Vue.component('VueRecaptcha', VueRecaptcha)
Vue.component(Button.name, Button)
Vue.component(InputNumber.name, InputNumber)
Vue.component(Slider.name, Slider)
Vue.component(Select.name, Select)
Vue.component(Switch.name, Switch)
Vue.component(Option.name, Option)
Vue.component(Pagination.name, Pagination)
Vue.component(Dialog.name, Dialog)
Vue.component(Tooltip.name, Tooltip)
Vue.component(Rate.name, Rate)
Vue.component(Input.name, Input)
Vue.component(Alert.name, Alert)
Vue.component(Upload.name, Upload)
Vue.component(DatePicker.name, DatePicker)
Vue.component('adsense', VueAdsense)
Vue.use(VueAwesomeSwiper)
Vue.use(VueRouter)
Vue.use(Resource)
Vue.use(VueProgressBar, {
  color: '#29B6F6',
  failedColor: '#874b4b',
  thickness: '3px',
  transition: {
    speed: '0.2s',
    opacity: '0.6s'
  }
})
Vue.use(VueResourceProgressBarInterceptor)
Vue.use(Meta, {
  keyName: 'metaInfo', // the component option name that vue-meta looks for meta info on.
  tagIDKeyName: 'vmid' // the property name that vue-meta uses to determine whether to overwrite or append a tag
})
Vue.use(DataTable)
Vue.use(SocialSharing)
Vue.use(VeeValidate)
Vue.use(VueQuillEditor)
Vue.use(VueSweetAlert)

Vue.component('InfiniteLoading', InfiniteLoading)
Vue.component('ClipLoader', PulseLoader)



var router = new VueRouter({
  routes: routes,
  mode: 'history'
})

router.beforeEach((to, from, next) => {
  document.title = to.meta
  if (typeof to.meta === 'string') {
    document.title = 'Best Bargains - ' + to.meta
  } else {
    document.title = 'Best Bargains - ' + to.path.split('/')[3]
  }
  if (!store.getters.isEmpty) {

    router.app.$http.get(path + '/stores').then((response) => {
      store.dispatch('setList', response.body)
      store.dispatch('setObj', _.keyBy(response.body, (o) => { return o._id}))
    }, () => {
    })
  }
  next()
})

Vue.http.interceptors.push((request, next) => {
  var header = store.getters.getToken
  if (header !== null) {
    header = 'Bearer ' + header
    request.headers.set('Authorization', header)
  }
  next()
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<div><App/><vue-toastr ref="toastr"></vue-toastr></div>',
  components: {
    App,
    'vue-toastr': Toastr
  },
  data () {
    return {
      title: "BB WEB"
    }
  },
  created () {
  },
  mounted () {
    this.$refs.toastr.defaultTimeout = 2000
    this.$refs.toastr.defaultPosition = 'toast-top-center'
  },
  store,
  router: router
})



